import React from 'react';

import cover from './test-cover.svg';
import left from './icon-left.svg'
import right from './icon-right.svg'

import Layout from '../Layout';
import Box from 'components/Box'
import Container from 'components/Container'
import Text from 'components/Text'
import Button from 'components/Button';
import Image from 'components/Image'
import Link from 'components/Link'
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'
import { mobileOrDesktop } from 'components/ThemeProvider/theme';

const Start = () => {
  return (
    <Layout bg="bgOrange" overflow="hidden">
      <Box height="100%" textAlign="center" position="relative">
        <FullpageVerticalCenter>
          <Container>
            <Box
              bg="white"
              display="inline-block"
              py="3em"
              px="2em"
              borderRadius="1em"
              mx={mobileOrDesktop('1em', 'unset')}
              color="titleBlue"
              fontWeight="bold"
            >
              <Image src={cover} px={mobileOrDesktop('5%', '20%')} />
              <Text.serif fontSize="2.5em" mt="0.5em" >8題問答</Text.serif>
              <Text.serif fontSize="1.5em" >測測我的看診表現</Text.serif>
              <Text my="1em" px="10%" textAlign="center" color="black" fontWeight="normal">根據問題選擇圈叉，送出後就可以知道你的看診表現分數以及應該注意的事項！</Text>
            </Box>
            <Box mt="2.5em">
              <Button is={Link} px="3em" to="/test">開始測驗</Button>
            </Box>
            <Text mt="1em">(系統將不會搜集你的填答紀錄，請放心填寫)</Text>
          </Container>
        </FullpageVerticalCenter>
        <Box>
          <Box position="absolute" left={mobileOrDesktop('-11%', '2%')} bottom={mobileOrDesktop('11%', '4%')} width={mobileOrDesktop('22%', '10%')}>
            <Image src={left} />
          </Box>
          <Box position="absolute" right={mobileOrDesktop('-11%', '2%')} bottom={mobileOrDesktop('-2%', '4%')} width={mobileOrDesktop('22%', '10%')}>
            <Image src={right} />
          </Box>
        </Box>
      </Box>
    </Layout>
  );
};

export default Start;
